.flow-list-header {
  display: flex;
}
.flow-list-header .col-expanded {
  flex: 1;
}
.flow-list {
  padding: 24px;
  background-color: #fff;
}
.flow-list .flow-search-bar {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 16px;
}
.flow-list-main .rc-table-content,
.flow-list-main .rc-table th,
.flow-list-main .rc-table td {
  border: 0;
}
.flow-list-main .rc-table-row:hover td {
  background-color: rgba(87, 138, 236, 0.15);
}
.flow-list-main .rc-table-cell-fix-right-first,
.flow-list-main .rc-table-cell-fix-right-last {
  box-shadow: -1px 0 0 transparent;
}
