.flow-record .flow-record-item {
  position: relative;
  display: flex;
  min-height: 48px;
  padding-bottom: 30px;
}
.flow-record .flow-record-item:nth-of-type(1) {
  color: #578AEC;
}
.flow-record .flow-record-item:nth-of-type(1) .flow-mark-circle {
  background-color: #578AEC;
}
.flow-record .flow-record-item:nth-of-type(1) .flow-record-item-mark::after {
  background: linear-gradient(180deg, #578AEC 1%, #CCCCCC 100%);
}
.flow-record .flow-record-item:nth-last-of-type(1) {
  padding-bottom: 0;
}
.flow-record .flow-record-item:nth-last-of-type(1) .flow-record-item-mark::after {
  content: none;
}
.flow-record .flow-record-item-label {
  margin-right: 10px;
}
.flow-record .flow-record-item-text {
  text-align: right;
}
.flow-record .flow-record-item-mark {
  position: relative;
  margin-top: 3px;
  align-self: stretch;
}
.flow-record .flow-record-item-mark .flow-mark-circle {
  position: relative;
  top: 4px;
  width: 8px;
  height: 8px;
  z-index: 2;
  border-radius: 50%;
  background-color: #CCCCCC;
}
.flow-record .flow-record-item-mark::before,
.flow-record .flow-record-item-mark::after {
  content: '';
  position: absolute;
  left: 50%;
  display: block;
  height: 8px;
  transform: translate(-50%, 0);
}
.flow-record .flow-record-item-mark::before {
  width: 8px;
  box-sizing: content-box;
  border-radius: 50%;
  border: 4px solid rgba(0, 0, 0, 0.04);
}
.flow-record .flow-record-item-mark::after {
  width: 1px;
  height: calc(100% + 30px);
  background-color: #ccc;
}
.flow-record .flow-record-item-content {
  margin-left: 20px;
}
.flow-record .flow-record-item-content .flow-item-content-title {
  font-size: 14px;
  font-weight: bold;
}
.flow-record .flow-record-item .flow-item-content-row {
  display: flex;
  margin: 6px 0;
}
.flow-record .flow-record-item .flow-attachments-image {
  width: 120px;
  height: 120px;
}
.flow-record .flow-record-item .flow-attachments-image img {
  display: block;
  width: 100%;
}
.flow-record .flow-record-item .flow-attachments-link {
  margin-bottom: 4px;
}
.flow-record .flow-record-item .flow-attachments-link:nth-last-child(1) {
  margin-bottom: 0;
}
